








































































































































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import Button from '@/components/Button.vue';
import Chart from '@/components/Chart.vue';
import _ from 'lodash';

@Component({
  components: {Chart, Button, Tabs}
})

export default class Statistics extends Vue {
  get keyValueList(){
    const today = new Date();
    const array = [];
    for(let i=0; i<= 29; i++){
      const dateString = dayjs(today).subtract(i,'day').format('YYYY-MM-DD');
      const found = _.find(this.groupList,{
        title: dateString
      });
      array.push({
        key: dateString, value: found ? found.total: 0
      })
    }
    array.sort((a,b)=>{
      if (a.key > b.key){
        return 1;
      }else if(a.key === b.key){
        return 0;
      }else {
        return -1;
      }
    });
    return array;
  }
  get chartOptions(){
    const keys = this.keyValueList.map(item => item.key);
    const  values = this.keyValueList.map(item => item.value);
    return {
      grid:{
        left:0,
        right:0,
        top:16,
        bottom:40
      },
      tooltip: {
        trigger: 'axis'
      },
      xAxis: {
        type: 'category',
        data: keys,
        axisTick:{show: false,alignWithLabel: true},
        axisLine: {lineStyle:{color: '#666'}},
        axisLabel:{
          formatter: (value: string) =>{
            return value.substr(5)
          }
        }
      },
      yAxis: {
        type: 'value'
      },
      series: [{
        symbol:'circle',
        symbolSize:8,
        itemStyle:{color: '#666'},
        data: values,
        type: 'line'
      }]
    };
  }
  leftStyle = 0;
  startX = 0;
  hiddenFlag = true;
  delBtnWidth = 40;
  element: Element | null | undefined = null;

  touchS({touches}: TouchEvent) {
    console.log(touches);
    /*
    * 兼容IE 移动端 开始
    * */
    if (!Element.prototype.matches)
      Element.prototype.matches = Element.prototype.msMatchesSelector ||
        Element.prototype.webkitMatchesSelector;

    if (!Element.prototype.closest)
      Element.prototype.closest = function (s: string) {
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        let el: Element | null = this;
        if (!document.documentElement.contains(el)) return null;
        do {
          if (el.matches(s)) return el;
          el = el.parentElement;
        } while (el !== null);
        return null;
      };
    /*
    * 兼容IE 移动端 结束
    * */
    this.element = document.elementFromPoint(touches[0].clientX, touches[0].clientY)?.closest('div');
    if (touches.length === 1) {
      this.startX = touches[0].clientX;
    }
    this.hiddenFlag = this.leftStyle === 0;
  }

  touchM({touches}: TouchEvent) {
    if (touches.length === 1) {
      const moveX = touches[0].clientX;
      this.moveFunc(moveX);
    }
  }

  touchE() {
    const {leftStyle} = this;
    const {delBtnWidth} = this;
    if (-leftStyle > delBtnWidth / 2) {
      this.setLeft(-delBtnWidth);
    } else {
      this.setLeft(0);
    }
  }

  moveFunc(moveX: number) {
    const disX = moveX - this.startX;
    const delBtnWidth = this.delBtnWidth;
    let offsetNum = 0;
    if (-disX >= delBtnWidth && this.leftStyle === -delBtnWidth) {
      return;
    }
    if (this.hiddenFlag) {
      if (disX === 0 || disX > 0) {
        offsetNum = 0;
      } else {
        offsetNum = disX;
        if (disX <= -delBtnWidth) {
          offsetNum = -delBtnWidth;
        }
      }
    } else {
      if (disX < 0) {
        offsetNum = -this.delBtnWidth;
      } else {
        offsetNum = -this.delBtnWidth + disX;
        if (offsetNum > 0) {
          offsetNum = 0;
        }
      }
    }
    this.setLeft(offsetNum);
  }

  setLeft(leftStyle: number) {
    this.leftStyle = leftStyle;
    !(this.element === null || this.element === undefined) && this.element.setAttribute('style', `left: ${leftStyle}px`);
  }

  remove(id: string) {
    if (confirm('你确定要删除吗')) {
      this.$store.commit('removeRecords', id);
    }
  }


  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, 'day')) {
      return '今天';
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return '昨天';
    } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
      return '前天';
    } else if (day.isSame(now, 'year')) {
      return day.format('M月D日');
    } else {
      return day.format('YYYY年M月D日');
    }
  }

  get recordList() {
    return (this.$store.state as RootState).recordList;
  }


  get groupList() {
    type Result = { title: string; total?: number; items: RecordItem[] }[]
    const {recordList} = this;

    const newList = clone(recordList).filter(r => r.type === this.type).sort((a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
    if (newList.length === 0) {return [];}
    const result: Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), items: [newList[0]]}];
    let last = dayjs(newList[0].createdAt).format('YYYY-MM-DD');
    for (let i = 1; i < newList.length; i++) {
      if (dayjs(newList[i].createdAt).format('YYYY-MM-DD') === last) {
        result[result.length - 1].items.push(newList[i]);
      } else {
        result.push({title: dayjs(newList[i].createdAt).format('YYYY-MM-DD'), items: [newList[i]]});
        last = dayjs(newList[i].createdAt).format('YYYY-MM-DD');
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
    });
    return result;
  }

  mounted() {
    this.$store.commit('fetchRecords');
    const div = this.$refs.chartWrapper as HTMLDivElement;
    div.scrollLeft = div.scrollWidth;
  }

  type = '-';
  recordTypeList = recordTypeList;
}
